import { LitElement, html } from 'lit';

import { styles } from './merch-subscription-layout.css.js';

const TAG_NAME = 'merch-subscription-layout';

export class SubscriptionLayout extends LitElement {
    static styles = [styles];
    /**
     * Merch cards element, currently connected to this modal.
     */
    cards;

    /**
     * Merch subscription panel, connected to this modal.
     */
    panel;

    tabs = { individual: [], business: [], students: [] };

    connectedCallback() {
        super.connectedCallback();
        this.cards = this.querySelectorAll('merch-card');
        this.panel = this.querySelector('merch-subscription-panel');

        if (this.panel && this.cards.length) {
            let pricePromises = [];
            [...this.cards].forEach((card) => {
                const prices = card.querySelectorAll(
                    '[is="inline-price"][data-template="price"]'
                );
                if (prices.length) {
                    pricePromises = [
                        ...pricePromises,
                        ...[...prices].map((price) => price.onceSettled()),
                    ];
                }
            });

            Promise.all(pricePromises).then(() => {
                this.cards.forEach((card) => {
                    // todo should be moved to the card itself?
                    const price = card.querySelector(
                        '[is="inline-price"][data-template="price"]'
                    );
                    if (!price.value?.length) return;
                    const { marketSegments, customerSegment } = price.value[0];
                    if (!marketSegments?.length) return;
                    if (
                        marketSegments[0] === 'COM' &&
                        customerSegment === 'INDIVIDUAL'
                    ) {
                        this.tabs.individual = [...this.tabs.individual, card];
                    } else if (marketSegments[0] === 'COM') {
                        this.tabs.business = [...this.tabs.business, card];
                    } else if (marketSegments[0] === 'EDU') {
                        this.tabs.students = [...this.tabs.students, card];
                    }
                });
                if (this.tabs.individual && this.tabs.individual.length)
                    this.panel.card = this.tabs.individual[0];
            });
        }
    }

    render() {
        return html`
            <sp-theme theme="spectrum" color="light" scale="medium">
                <div id="layout">
                    <div id="cards">
                        <slot name="cards"></slot>
                    </div>
                    <div id="panel">
                        <slot name="panel"></slot>
                    </div>
                </div>
            </div>
        `;
    }
}

window.customElements.define(TAG_NAME, SubscriptionLayout);
